body {
  color: #fff;
  background-color: #12181b;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h1 {
  text-align: center;
  padding: 1rem;
  border-radius: 10px 10px 30px rgba(0, 0, 0, 0.1);
  background-color: #383838;
  box-shadow: 0 4px 8px hsl(0deg 0% 0% / 38%);
}

.card {
  margin: 1rem;
  padding: 1rem;
  border-radius: 10px 10px 30px rgba(0, 0, 0, 0.1);
  background-color: #383838;
  display: flex;
  justify-content: center;
  width: 40rem;
  margin: 3rem auto;
  box-shadow: 0 4px 8px hsl(0deg 0% 0% / 38%);

  .imgs {
    height: 200px;
    margin: 1rem;

    img {
      height: 160px;
    }
  }

  .infos {
    margin: 1rem;
  }
}

.btns {
  display: flex;
}
#btn {
  text-align: center;
  margin: 0 auto;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 0.6rem;
  outline: none;
  border: none;
  width: 7rem;
  padding: 0.55rem;
  cursor: pointer;
  color: #fff;
  border-radius: 4px;
  background-color: hsl(217deg 99% 65%);
}
